.home-title {
    color: black;
    text-align: center;
    letter-spacing: 10px;
    margin: 0 auto;
    background-image: url("../../../assets/img/image-1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    border: 1px ridge goldenrod;
    position: relative;
    opacity: .8;
    min-height:600px;
    height: 85vh;
    background-position: bottom;
    border-radius: 8px;

    .big-text {
        font-size: 70px;
        font-weight: 500;
        // font-family: 'Fleur De Leah', cursive;
        padding: 30px;

        @media (max-width: 528px) {
            font-size: 40px;
        }
    }
    .small-text {
        font-size: 30px;
        font-weight: 900;
        letter-spacing: 10px;
        padding: 20px;

        @media (max-width: 528px) {
            font-size: 24px;
        }
    }

    @media (min-width: 1600px) {
        min-height: 900px;
    }
}

.about-us {
    border-radius: 24px;
    width: 100%;
    margin: 0 auto;
    margin-top: 75px;

    .our-story {

        display: flex;
        flex-direction: column;
        margin: 0 auto;
        justify-content: center;
        align-items: center;

        .our-story-row {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px;
            width: 100%;
            max-width: 900px;
            max-height: 100%;
            border-radius: 8px;
            padding: 10px;
            border: 1px ridge goldenrod;
            background-color: whitesmoke;
            object-fit: cover;

            .image {
                margin: 0 auto;
                align-self: center;

                img {
                    max-width: 100%;
                    max-height: 100%;
                    border-radius: 6px;
                    object-fit: cover;
                }
            }

            .text {

                border-radius: 8px;
                width: 100%;
                height: 100%;
                padding: 8px;
                display: flex;
                flex-direction: column;
                grid-gap: 10px;
                
                .subheader {
                    font-size: 26px;
                    font-weight: 600;
                }
                .subheader-break {
                    border-style: ridge;
                    height: 1px;
                    width: 100%;
                }
                .subheader-small {
                    font-size: 20px;
                    font-style: italic;
                }
                .description {
                    font-size: 20px;
                    word-wrap: break-word;
                }
            }

            @media (max-width: 528px) {
                grid-template-columns: 1fr;
            }
        }
    }
    .gallery-break {
        display: inline-block;
        margin: 0 50%;
        border-left: 5px dotted rgba(218, 165, 32, 0.37);
        height: 100px;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    @media (max-width: 900px) {
        width: 100%;
    }
}